import { Component, OnInit, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Product, Price } from 'src/app/models/product.model';
import { environment } from 'src/environments/environment';

export interface OrderProductItem {
  product: Product;
  product_id: number;
  quantity: number;
  price: Price;
}

@Component({
  selector: 'app-order-products-form',
  templateUrl: './order-products-form.component.html',
  styleUrls: ['./order-products-form.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => OrderProductsFormComponent),
      multi: true
    }
  ]
})
export class OrderProductsFormComponent implements OnInit, ControlValueAccessor {
  public products: Product[];
  public data: OrderProductItem[];

  orderProductItem: FormGroup;

  constructor(private http: HttpClient) {
    this.http.get<Product[]>(environment.prepareUrl('productsFetchUrl')).subscribe(data => this.products = data);

    this.orderProductItem = new FormGroup({
      product_id: new FormControl(null, Validators.required),
      quantity: new FormControl(1, [Validators.required, Validators.min(1)]),
    });
  }

  onChange: any = () => {

  }

  onTouch: any = () => {

  }

  set value(items: OrderProductItem[]) {
    this.data = items;
    this.onChange(items);
    this.onTouch(items);
  }

  writeValue(value: OrderProductItem[]): void {
    if (!value)
    {
      value = [];
    }
    this.value = value;
  }

  registerOnChange(cb: any): void {
    this.onChange = cb;
  }

  registerOnTouched(cb: any): void {
    this.onTouch = cb;
  }

  ngOnInit(): void {
  }

  public get activeProductsList(): Product[] {
    if (!this.products)
    {
      return [];
    }
    else if (!Array.isArray(this.data) || this.data.length === 0)
    {
      return this.products;
    }
    else
    {
      return this.products.filter(p => !this.data.some(e => e.product_id.toString() === p.id.toString()));
    }
  }

  public get total(): number {
    if (!this.data)
    {
      return 0;
    }
    let t = 0;
    this.data.forEach(item => t += item.quantity * item.price.value);
    return t;
  }


  addOrderProductItem(): void {
    const product: Product = this.products.find(p => p.id === Number.parseInt(this.orderProductItem.value.product_id, 10));
    const item: OrderProductItem = {
      product,
      price: product.price,
      ...this.orderProductItem.value,
    };

    this.data.push(item);
    this.orderProductItem.reset();
    this.writeValue(this.data);
  }

  removeOrderProduct(item: OrderProductItem): void {
    this.data.splice(this.data.indexOf(item), 1);
    this.writeValue(this.data);
  }

  editOrderProduct(item: OrderProductItem): void {
    this.removeOrderProduct(item);
    this.orderProductItem.reset({
      product_id: item.product_id,
      quantity: item.quantity,
    });
  }
}
