import { Component, OnInit, forwardRef, Input } from '@angular/core';
import { ControlValueAccessor, FormGroup, FormControl, Validators, NG_VALUE_ACCESSOR } from '@angular/forms';
import { OrderClientInformation } from 'src/app/models/order.model';
import { HttpClient } from '@angular/common/http';
import { User } from 'src/app/models/user.model';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-order-client-form',
  templateUrl: './order-client-form.component.html',
  styleUrls: ['./order-client-form.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => OrderClientFormComponent),
      multi: true
    }
  ]
})
export class OrderClientFormComponent implements OnInit, ControlValueAccessor {
  @Input()
  form: FormGroup;
  public data: OrderClientInformation;
  public users: User[];

  constructor(private http: HttpClient) {
    this.http.get<User[]>(environment.prepareUrl('usersFetchUrl')).subscribe(data => this.users = data);
  }

  onChange: any = () => {

  }

  onTouch: any = () => {

  }

  set value(information: OrderClientInformation) {
    this.data = information;
    this.onChange(information);
    this.onTouch(information);
  }

  writeValue(value: OrderClientInformation): void {
    this.value = value;
  }

  registerOnChange(cb: any): void {
    this.onChange = cb;
  }

  registerOnTouched(cb: any): void {
    this.onTouch = cb;
  }

  ngOnInit(): void {
    console.log(this.form);
    this.form.valueChanges.subscribe(e => {
      console.log('xx');
      this.writeValue(this.form.value);
    });
  }

}
