import { Component, OnInit } from '@angular/core';
import { Order } from 'src/app/models/order.model';
import { ResourceDeleteContext } from 'src/app/shared/dashboard-resource/resource-delete/resource-delete.component';

@Component({
  selector: 'app-order-delete',
  templateUrl: './order-delete.component.html',
  styleUrls: ['./order-delete.component.scss']
})
export class OrderDeleteComponent implements OnInit {
  context: ResourceDeleteContext<Order>;

  constructor() { }

  ngOnInit(): void {
    this.context = {
      deleteUrl: 'ordersDeleteUrl',
      data: history.state.resource,
      title: 'Delete Order Resource!',
      description: 'Are you sure to delete this order?'
    };
  }
}
