import { Component, OnInit, ViewChild } from '@angular/core';
import { Order } from 'src/app/models/order.model';
import { ITableContext, TableViewComponent } from 'src/app/shared/dashboard-resource/table-view/table-view.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent {
  data: Order[];
  context: ITableContext<Order> = {
    urls: {
      createUrl: '/orders/create',
      detailsUrl: '/:id'
    },
    data: this.data,
    properties: [
      'status_id', 'client_id'
    ],
    title: 'Orders',
    actions: [
      {
        icon: 'nc-icon nc-bell-55',
        type: 'url',
        url: ':id/status'
      }
    ]
  };
  @ViewChild('table') table: TableViewComponent<Order>;

  constructor(route: ActivatedRoute) {
    // console.log(this.context);
    route.data.subscribe(data => {
      this.data = data.orders;
      this.context.data = this.data;
    });
  }
}
