import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ResourceFormContent } from 'src/app/shared/dashboard-resource/resource-form/resource-form.component';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-order-form',
  templateUrl: './order-form.component.html',
  styleUrls: ['./order-form.component.scss']
})
export class OrderFormComponent extends ResourceFormContent {
  title: string;
  message: string;
  formGroup: FormGroup;

  @ViewChild('content') content: ElementRef<HTMLElement>;

  constructor(http: HttpClient, route: ActivatedRoute, router: Router, service: NgbModal) {
    super(http, route, router, service);

    this.formGroup = new FormGroup({
      promo_code: new FormControl(null, [Validators.minLength(8), Validators.maxLength(8)]),
      products: new FormControl(null, Validators.required),
      client: new FormGroup({
        name: new FormControl(null, [Validators.required, Validators.minLength(3)]),
        email: new FormControl(null, [Validators.required, Validators.email]),
        phone: new FormControl(null, [Validators.required]),
        province: new FormControl(null, [Validators.required]),
        address: new FormControl(null, [Validators.required]),
        notes: new FormControl(null, [Validators.minLength(10)]),
        user_id: new FormControl(null)
      }),
    });

    console.log(this.formGroup.controls.client);
  }

  openModal(): NgbModalRef {
    const options: NgbModalOptions = {
      backdrop: 'static'
    };

    return this.service.open(this.content, options);
  }

  postUrl(): string {
    return 'ordersCreateUrl';
  }
  putUrl(): string {
    return 'ordersUpdateUrl';
  }
  xx(): void {
    console.log(this.formGroup);

  }
}
